<template>
  <div class="content_box">
     <el-row>
        <el-col :span="10" :offset="6" class="form-content">
          <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="150px" class="demo-ruleForm">
            <el-form-item label="用户名" prop="name" class="item_height">
              <el-input v-model="ruleForm.name"/>
            </el-form-item>
            <el-form-item label="账号" class="item_height">
              <el-input v-model="ruleForm.account" disabled/>
            </el-form-item>
            <el-form-item label="归属分组" class="item_height">
              <el-input v-model="ruleForm.group" disabled/>
            </el-form-item>
            <el-form-item label="密码" class="item_height">
              <el-input v-model="ruleForm.password"/>
            </el-form-item>
            <el-form-item label="确认密码" class="item_height">
              <el-input v-model="ruleForm.pwd"/>
            </el-form-item>
            <el-form-item label="备注" class="item_height">
               <el-input v-model="ruleForm.remark" type="textarea" :rows="6" />
            </el-form-item>
            <el-form-item class="saveItem">
              <el-button class="queryBtn">保存</el-button>
            </el-form-item>
          </el-form>
        </el-col>
     </el-row>
  </div>
</template>
<script>
export default {
  name:'MyInfo',
  data(){
    return {
      ruleForm:{
        name:'可修改',//用户名
        account:'不可修改',//账号
        group:'不可修改',//归属分组
        password:'可修改',//密码
        pwd:'可修改',//再次输入密码
        remark:'可修改',//备注
      },
      rules:{
        name: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
        ],
      }
    }
  },
  methods:{

  }
}
</script>
<style lang="less" scoped>
.form-content{
  font-size:14px;
  font-family: 'PingFang-SC-Medium';
  font-weight:400;
  .item_height{
    margin: 40px 0;
  }
}
.saveItem{
  text-align: center;
  /deep/ .el-form-item__content{
    margin-left: 90px !important;
  }
}
</style>




